import React from 'react';
import { connect } from 'react-redux';
import { Field, reduxForm } from 'redux-form';
import { required } from '../../utils/validators/validators';
import { createField, Input } from '../common/preloader/FormsControls/FormsControls';
import {login} from '../../redux/auth-reducer';
import { Redirect } from 'react-router';
import styles from '../common/preloader/FormsControls/FormsControls.module.css'

const LoginForm = ({handleSubmit, error}) => {
    return (
            <form onSubmit={handleSubmit}>
                    {createField("Email", "email", [required], Input)}
                    {/* <Field placeholder={"Email"} name={"email"}
                        validate={[required]} component={Input} /> */}
                    
                    {createField("Password", "password", [required], Input, {type: "password"})}
                    {/* <Field placeholder ={"Password"} name={"password"} type={"password"}
                        validate={[required]} component={Input} /> */}
                
                    {/* {createField(null, "rememberMe", [required], Input, {type: "checkbox"}, "remember me")}  */}
                    <Field component="input" type={"checkbox"} name={"rememberMe"}/> remember me
                
               { error && <div className={styles.formSummaryError}>
                     {error}
                </div> }
                <div>
                    <button>Login</button>
                </div>
            </form>
    )
}

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm);


const Login = (props) => {
    const onSubmit = (formData) =>{
      props.login(formData.email, formData.password, formData.rememberMe); 
    }
    if(props.isAuth){
        return <Redirect to={"/profile"} />
    }
    return (
        <div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit} />
        </div>
    )
}
const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})
export default connect(mapStateToProps, {login})(Login);   